


.input {
  margin-top: 20px;
  padding: 7px;
  width: 350px;
  border-radius: 10px;
  border: 1px solid olivedrab;
}

.button {
  padding: 5px 15px;
  height: 30px;

  font-size: 16px;

  background: #6fb0e3;
  border: none;
  border-radius: 10px;

  cursor: pointer;

  transition: all 350ms linear;
}

.button:hover {
  background-color: rgba(106, 203, 220, 0.71);
  color: rgba(52, 50, 50, 0.58);
}

.button:active {
  box-shadow: rgba(37, 37, 58, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
